.success-container {
  width: 400px;
  height: 455px;
  background: #FFFFFF;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;

  .icon {
    height: 35px;
    width: 35px;
    border-radius: 0px;
  }

  .title {
    width: 198px;
    height: 27px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    letter-spacing: -0.035em;
    color: #4F4F4F;
  }

  .image {
    width: 338px;
    height: 224.4px;
    border-radius: 12px;
  }

  .link-container {
    width: 338px;
    height: 34px;
    background: #F6F8FB;
    border: 1px solid #E0E0E0;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    .link {
      width: 240px;
      height: 12px;
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      text-align: center;
      letter-spacing: -0.035em;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #4F4F4F;
    }

    .copy-button {
      width: 74px;
      height: 30px;
      background: #2F80ED;
      border-radius: 8px;
      border: 1px solid #2F80ED;
      outline: none;
      font-weight: 500;
      font-size: 8px;
      line-height: 12px;
      text-align: center;
      letter-spacing: -0.035em;
      color: #FFFFFF;
      cursor: pointer;
      margin-left: 14px;
    }
  }
}