.drag-container {
  height: 469px;
  width: 402px;
  border-radius: 12px;
  background-color: #FFFFFF;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);

  .title {
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    letter-spacing: -0.035em;
    margin: 20px auto;
    background-color: #FFFFFF;
    width: 165px;
    height: 27px;
  }

  .sub-title {
    width: 121px;
    height: 15px;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 15px;
    text-align: center;
    margin: 0 auto 20px auto;
    letter-spacing: -0.035em;
    color: #828282;
  }

  .drag-area {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 219px;
    width: 338px;
    background: #F6F8FB;
    border: 1px dashed #97BEF4;
    border-radius: 12px;
    margin: 0 auto;

    .icon {
      height: 70px;
      width: 90px;
      margin-top: 20px;
      color: #BDBDBD;
    }

    .drag-title {
      font-family: Poppins;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      letter-spacing: -0.035em;
      text-align: left;
      color: #BDBDBD;
      margin-top: 20px;
    }
  }

  .or {
    width: 14px;
    height: 17.99px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    letter-spacing: -0.035em;
    color: #BDBDBD;
    margin: 20px auto 20px auto;
  }

  .label {
    height: 32px;
    margin: auto;
    cursor: pointer;
    width: 101px;
    padding: 10px;
    border-radius: 8px;
    background: #2F80ED;
    color: white;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: -0.035em;
    text-align: center;
  }

  .label-text {
    background: #2F80ED;
    cursor: pointer;
  }
}